import 'ol/ol.css';
import Map from 'ol/Map';
import VectorSource from 'ol/source/Vector';
import View from 'ol/View';
import XYZ from 'ol/source/XYZ';
import Point from 'ol/geom/Point';
import OSM from 'ol/source/OSM';
import sVector from 'ol/source/Vector';
import lVector from 'ol/layer/Vector';
import {Collection, Feature} from 'ol';
import {ScaleLine, defaults as defaultControls} from 'ol/control';
import {Circle as CircleStyle, Fill, Stroke, Style, Icon} from 'ol/style';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import {fromLonLat} from 'ol/proj';
import WMTS, {optionsFromCapabilities} from 'ol/source/WMTS';
import WMTSCapabilities from 'ol/format/WMTSCapabilities';
import {DEVICE_PIXEL_RATIO} from 'ol/has';

var url = "https://paul42812.github.io/stplv2assets/"

//Style for Icons
var iconStyle = new Style({
  image: new Icon(({
    anchor: [0.5, 18], 
    anchorXUnits: 'fraction',
    anchorYUnits: 'pixels',
    src: 'https://www.dropbox.com/s/fx852dq393bwjj3/icon.png?dl=1'
  }))
});

const map = new Map({
  controls: defaultControls().extend([
      new ScaleLine({
        units: "metric",
      })
    ]),
  target: 'map',
  view: new View({
    center: fromLonLat([15.6274, 48.2087]),
    zoom: 13,
  }),
});


//Load Basemap
const capabilitiesUrl = 'https://basemap.at/wmts/1.0.0/WMTSCapabilities.xml';
const hiDPI = DEVICE_PIXEL_RATIO > 1;
const layer = hiDPI ? 'bmaphidpi' : 'geolandbasemap';
const tilePixelRatio = hiDPI ? 2 : 1;

fetch(capabilitiesUrl)
  .then(function (response) {
    return response.text();
  })
  .then(function (text) {
    const result = new WMTSCapabilities().read(text);
    const options = optionsFromCapabilities(result, {
      layer: layer,
      style: 'normal',
    });
    options.tilePixelRatio = tilePixelRatio;
    options.attributions =
      'Basemap: <a target="_blank" href="https://basemap.at/">basemap.at</a>';
    
    var lay = new TileLayer({
        source: new WMTS(options),
    })

    map.getLayers().insertAt(0, lay);
  });

$(document).ready(function() {
  $.ajax({
      type: "GET",
      url: "http://localhost/icons.csv",
      dataType: "text",
      success: function(data) {
        display(data);
      }
   });
});

function display(data){
  var k = data.split("\n")

  //console.log(k);

  var icons = [];

  for(var i = 1; i < k.length; i++) {
    console.log(k[i]);
    a = k[i].split(",");

    icons.push(
      new Feature({
        geometry: new Point(fromLonLat([a[5],a[4]])),
        image: a[0]+"/"+a[1]+"/"+a[2]+"/"+a[3]
      })
    );
  }
  icons.forEach(element => {
      element.setStyle(iconStyle)
    });

    var vectorSource = new sVector({
      features: icons
    });

    var abc = new lVector({
      source: vectorSource
    });

    map.addLayer(abc);
};


map.on('click', function(e) {
    //Click and zoom on Gemeinden
    var f = map.forEachFeatureAtPixel(
        e.pixel,
        function(ft, layer){return ft;}
    ); 
    
    if(f){
      console.log(f.get('image'));
      document.getElementById("img").src = url+"Bilder/"+f.get('image');
      
    } 
        

});
